<template>
  <div class="ma-3">
    <div class="temp-animator"></div>
    <div style="min-height: 50px; display: none">
      <div class="write-header">
        <div class="left">
          <input
            class="input-box"
            value="2 February 5 pm"
            @input="list($event)"
            placeholder="Enter product name"
            type="text"
            name=""
            id="k-act"
            style="font-size: 20px; width: 100%; font-weight: bold; padding: 0"
          />
          <p>Auto-cancel time</p>
        </div>
        <div class="right">
          <input
            class="input-box"
            value="At my home"
            @input="list($event)"
            placeholder="Enter product name"
            type="text"
            name=""
            id="k-dl"
            style="
              font-size: 20px;
              width: 100%;
              font-weight: bold;
              padding: 0;
              text-align: right;
            "
          />
          <p style="text-align: right">Delivery Location</p>
        </div>
      </div>
    </div>
    <table>
      <tr>
        <td class="ripple">
          <Mic />
          <span id="tip-0"> And और: Next line </span>
        </td>
        <td class="ripple">
          <Mic />
          <span id="tip-1"> Wrong: Skip the line </span>
        </td>
      </tr>
      <tr>
        <td class="ripple">
          <Mic />
          <span id="tip-2"> Cancel: Auto cancel time </span>
        </td>
        <td class="ripple">
          <Mic />
          <span id="tip-3"> Price: Set max price </span>
        </td>
      </tr>
    </table>
    <div class="formatted">
      <input
        class="input-box"
        @input="list($event)"
        @blur="clearInput($event)"
        placeholder="Enter product name"
        type="search"
        name=""
      />
      <input
        placeholder="0000"
        type="number"

        class="input-price"
        id="prid-l"
      />
    </div>
    <button @click="downloadData()" style="margin-top: 20px; padding: 8px 30px; background: blue; border: none; border-radius: 8px; color: white">Download data</button>
  </div>
</template>

<script>
import Mic from "./Mic.vue";

function $C(id) {
  return document.getElementsByClassName(id);
}

export default {
  name: "Write.vue",
  components: {
    Mic,
  },
  data() {
    return {
      hideNav: false,
    };
  },
  mounted() {
    this.setTip();
    this.setAct();
  },
  methods: {
    hideNavi: function () {
      $C("bottoms")[0].style.display = "none";
    },
    showNavi: function () {
      $C("bottoms")[0].style.display = "block";
    },
    clearInput: function (e) {
      e.target.value = "";
    },
    setTip() {
      let i,
        tip = [
          { a: "And और: Next line", b: "And: Add Next" },
          { a: "Wrong: Skip the line", b: "Multi: then say ok" },
          { a: "Cancel: Auto cancel time", b: "More: to add product" },
          { a: "Price: Set max price", b: "Total: or click here" },
        ];
      for (i = 0; i < tip.length; i++) {
        document.getElementById("tip-" + i).innerText = tip[i].a;
      }
    },
    setAct() {
      const monthNames = [
        "Jan",
        "Feb,",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];
      let t = new Date();
      document.getElementById("k-act").value =
        t.getDate() +
        " " +
        monthNames[t.getMonth()] +
        " " +
        (t.getHours() + 4) +
        ":" +
        t.getMinutes();
    },

    list: function (e) {
      let input = e.target.value.toLowerCase();
      if (!e.data) return;
      let keys = [
        { a: "\\sand$", b: "and", c: "nl" },
        { a: "और$", b: "और", c: "nl" },
        { a: "ठीक$", b: "ठीक", c: "nl" },
        { a: "\\theek$", b: "theek", c: "nl" },
        { a: ",$", b: ",", c: "nl" },
        { a: "\\saur$", b: "aur", c: "nl" },
        { a: "wrong$", b: "wrong", c: "wrong" },
        { a: "गलत$", b: "गलत", c: "wrong" },
        { a: "ग़लत$", b: "ग़लत", c: "wrong" },
        { a: "cancel$", b: "cancel", c: "cl" },
        { a: "कैंसिल$", b: "कैंसिल", c: "cl" },
        { a: "location$", b: "location", c: "lc" },
        { a: "price$", b: "price", c: "pc" },
        { a: "प्राइस$", b: "प्राइस", c: "pc" },
        { a: "multi$", b: "multi", c: "*" },
        { a: "गुणा$", b: "गुणा", c: "*" },
        { a: "गुनाा$", b: "गुना", c: "*" },
        { a: "गुने$", b: "गुने", c: "*" },
        { a: "guna$", b: "guna", c: "*" },
        { a: "\\*", b: "guna", c: "*" },
        { a: "total$", b: "add", c: "+" },
        { a: "more$", b: "more", c: "." },
      ];
      let i,
        tip = [
          { a: "And और: Next line", b: "And: Add Next" },
          { a: "Wrong: Skip the line", b: "Multi: then say ok" },
          { a: "Cancel: Auto cancel time", b: "More: to add product" },
          { a: "Price: Set max price", b: "Total: or click here" },
        ];
      keys.forEach(function (value) {
        let lists = $C("formatted")[0];
        let last = lists.firstChild;
        let cln = last.cloneNode(true);
        let x = e.target.value.split(value.b);

        let lastInput = $C("input-box")[1];

        if (
          input.search(value.a) >= 0 &&
          value.c === "nl" &&
          document.getElementById("tip-0").innerText !== tip[0].b
        ) {
          cln.value = x.slice(-2)[0].trim();
          if (!cln.value) return;
          if (cln.value.search(value.a) >= 0) {
            try {
              lastInput.remove();
            } catch {
              console.log("");
            }
            e.target.value = "";
            return;
          }
          try {
            if (cln.value === lastInput.value) return;
          } catch {
            console.log("0");
          }

          keys.forEach(function (key) {
            if (cln.value.search(key.b) >= 0) {
              cln.value = cln.value.split(key.b)[1].trim();
            }
          });

          cln.addEventListener("search", function (event) {
            event.target.remove();
          });
          last.parentNode.insertBefore(cln, e.target.nextSibling);
        }

        if (input.search(value.a) >= 0 && value.c === "cl") {
          // cancel
          document.getElementById("k-act").value = cln.value = x
            .slice(-2)[0]
            .slice(-20)
            .trim();
        }
        if (input.search(value.a) >= 0 && value.c === "pc") {
          //price
          for (i = 0; i < tip.length; i++) {
            document.getElementById("tip-" + i).innerText = tip[i].b;
          }
        }
        if (input.search(value.a) >= 0 && value.c === "wrong") {
          // wrong
          console.log("");
        }

        // calculations starts here

        if (
          input.search(value.a) >= 0 &&
          document.getElementById("tip-0").innerText === tip[0].b
        ) {
          if (!document.getElementById("prid-1")) {
            // add price box
            $C("input-price")[0].style.opacity = "1";
            for (i = 0; i < $C("input-box").length - 3; i++) {
              let clp = $C("input-price")[0].cloneNode(true);
              clp.id = "prid-" + i;
              clp.addEventListener("input", function () {
                // price box method
                let k;
                for (k = 1; k < $C("input-price").length + 1; k++) {
                  if (!document.getElementById("prid-" + k).value) {
                    // document.getElementById('prid-'+i).value = price;
                    k = $C("input-price").length;
                    let j,
                      total = 0;
                    for (j = 0; j < $C("input-price").length; j++) {
                      if (!$C("input-price")[j].value) return;
                      total = parseInt($C("input-price")[j].value) + total;
                      document.getElementById("total-mp").innerText =
                        String(total);
                    }
                  }
                }
              });
              e.target.parentNode.insertBefore(
                clp,
                $C("input-box")[i + 2].nextSibling
              );
              if ($C("input-box").length - 4 === i) {
                document.getElementById("prid-l").id = "prid-" + (i + 1);
                let m = i + 1;

                // price box method for last
                document
                  .getElementById("prid-" + m)
                  .addEventListener("input", function () {
                    let k;
                    for (k = 1; k < $C("input-price").length + 1; k++) {
                      k = $C("input-price").length;
                      let j,
                        total = 0;
                      for (j = 0; j < $C("input-price").length; j++) {
                        if (!$C("input-price")[j].value) return;
                        total = parseInt($C("input-price")[j].value) + total;
                        document.getElementById("total-mp").innerText =
                          String(total);
                      }
                    }
                  });
              }
            }
            e.target.nextSibling.remove();
          }

          if (value.c === "nl") {
            // new line calc
            let priceA = x.slice(-2)[0].trim();
            let price = priceA.replace("price", "").trim();
            // keys.forEach(function (key) {
            //     if(price.search(key.b) >= 0){
            //         price = price.split(key.b)[1].trim();
            //     }
            // })
            price = Number(price.replace(/[^0-9.]+/g, "")); // digit only
            if (!price || price === 0) return;

            for (i = 1; i < $C("input-price").length + 1; i++) {
              if (document.getElementById("prid-" + i).value.search("x") >= 0) {
                // multiply
                let getInt = parseInt(
                  document.getElementById("prid-" + i).value.replace("x", "")
                );
                let priceX = Number(
                  price.toString().replace(getInt.toString(), "")
                );
                document.getElementById("prid-" + i).value = priceX * getInt;
                document.getElementById("prid-" + i).type = "number";
                i = $C("input-price").length + 1;
              } else if (!document.getElementById("prid-" + i).value) {
                document.getElementById("prid-" + i).value = price;
                i = $C("input-price").length;

              }
            }
          }
          if (value.c === ".") {
            // more product
            for (i = 0; i < tip.length; i++) {
              document.getElementById("tip-" + i).innerText = tip[i].a;
              $C("formatted")[0].id = "formatted-new";
            }
          }
          if (value.c === "+") {
            // total
            let j,
              total = 0;
            for (j = 0; j < $C("input-price").length; j++) {
              if (!$C("input-price")[j].value) return;
              total = parseInt($C("input-price")[j].value) + total;
              document.getElementById("tip-3").innerText =
                "Total Max price : " + String(total);
              document.getElementById("tip-3").style.color = "green";
            }
          }
          if (value.c === "*") {
            // multi
            let ret;
            for (i = 1; i < $C("input-price").length + 1; i++) {
              if (document.getElementById("prid-" + i).value.search("x") >= 0) {
                // multiply
                ret = true;
                i = $C("input-price").length + 1;
              }
            }
            if (ret) return;

            let priceA = x.slice(-2)[0].trim();
            let price = priceA.replace("price", "").trim();
            price = price.slice(-16);
            let priceX;
            let l, pl;
            for (l = 0; l < keys.length; l++) {
              if (keys[l].c === "nl") {
                let prices = price.split(keys[l].b);
                pl = prices.length;
                if (pl > 1) {
                  priceX = prices.slice(-2)[1].trim();
                  priceX = priceX.replace(/\D/g, "").trim();
                  l = keys.length;
                } else if (price.length < 12) {
                  priceX = price.replace(/\D/g, "").trim();
                }
              }
            }
            for (i = 1; i < $C("input-price").length + 1; i++) {
              if (!document.getElementById("prid-" + i).value) {
                document.getElementById("prid-" + i).type = "text";
                if (!priceX) return;
                document.getElementById("prid-" + i).value = priceX + "x";
                i = $C("input-price").length;
              }
            }
          }
          if (value.c === "wrong") {
            //wrong
            console.log("");
          }
        }
      });
    },
    order: function (e) {
      let va = localStorage.getItem("passCode"),
        vf = function () {
          let all = $C("input-box"),
            ord = "";
          all.forEach(function (o, i) {
            let u = o.value.replace(/\s/gi, "+");
            if (i !== 0) ord += "%0A";
            ord += u;
          });
          localStorage.setItem("order", ord);
          return ord;
        },
        vb = vf();
      //let src = 'https://docs.google.com/forms/d/e/1FAIpQLSedtSyRdQJT8ysY5XCnSuxhPaSYIaEDWo3G8ieaXXBjPoBaAg/formResponse?embedded=true?usp=pp_url&entry.1439677758='+va+'&entry.473497833='+vb+'&submit=Submit'

      let src =
        "https://docs.google.com/forms/d/e/1FAIpQLSc-bx8l9YjMoGzSPu8NUW1KFKWtcf4TSCk6pNgMKZ68CvfoGw/formResponse?embedded=true?usp=pp_url&entry.1087608269=name&entry.453208881=contact&entry.1755038076=" +
        va +
        "&entry.1458424290=" +
        vb +
        "&submit=Submit";

      if (!document.getElementById("iframeHolder").length) {
        document.getElementById("iframeHolder").innerHTML =
          '<iframe id="iframe" src="' +
          src +
          '" width="700" height="450" ></iframe>';
      }
      console.log(e.target);
    },
    downloadData: function () {
      let lists = $C('input-box'), prices = $C('input-price'), array = []
      for(let i = 0; i < prices.length; i++){
        array.push({
          n: lists[i+3].value,
          p: prices[i].value
        })
      }
      let data = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(array)), a = document.createElement('a')
      console.log(data)
      a.setAttribute('href', 'data: '+ data)
      a.setAttribute('download', 'data.json')
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
    }
  },
};
</script>

